@keyframes suites {
    from { opacity: 0; }
    to { opacity: 1; }
}

.suites{
    background-color: #3593f6;
    height: 10vh;
    font-size: 30px;
    width: auto;
    height: auto;
    border-radius: 1em;
    padding: 30px;
    margin: 10px;
    border: none;
    color: black;
    opacity: 0;
    animation: suites 0.5s ease-in forwards;
}

h1, fbacc{
    opacity: 0;
    animation: suites 0.5s ease-in forwards;
}


.button-container{
    width: 100vw;
    height: auto;
    border: 1px solid black;
}